import helpers from 'api/helpers';

function getObjectUrl(bucket, authURL, key, method) {
  try {
    const path = '/api/presigned-url';
    const params = { key, bucket, method };
    return helpers.get(authURL, path, params).then((response) => {
      if (response.Error) {
        /* eslint-disable no-console */
        console.log(response.Error);
        return '';
      }
      return response.URL;
    });
  } catch (error) {
    return error;
  }
}

async function validateDetails(bucket, authURL, details) {
  try {
    const path = '/api/details/validate';
    const urlParams = { bucket };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(urlParams).toString()}`;
    const options = {
      method: 'POST',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
      body: details,
    };
    return await fetch(apiUrl, options).then((response) => {
      if (!response.ok) {
        return { status: 'error' };
      }
      return response.json();
    });
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

async function upload(bucket, authURL, key, body) {
  try {
    const putURL = await getObjectUrl(bucket, authURL, key, 'PUT');
    const params = { body, method: 'PUT' };
    const resp = await fetch(putURL, params).then((response) => response);
    return resp;
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

async function deleteMemory(bucket, authURL, memoryID) {
  try {
    const path = '/api/memory';
    const params = { bucket, memory_id: memoryID };
    let apiUrl = `https://${authURL}${path}`;
    apiUrl += `?${new URLSearchParams(params).toString()}`;
    const options = {
      method: 'DELETE',
      credentials: 'include',
      headers: new Headers({ 'content-type': 'application/json' }),
    };
    return await fetch(apiUrl, options).then((response) => {
      if (!response.ok) {
        return { status: 'error' };
      }
      return { status: 'ok', body: response.json() };
    });
  } catch (error) {
    return error;
  }
}

function getThumbnails(memories) {
  try {
    const bucket = process.env.REACT_APP_S3_BUCKET;
    const authURL = process.env.REACT_APP_AUTH_URL;
    const path = '/api/memory/thumbnail';
    return memories.map(async (memoryID) => {
      const params = { bucket, memory_id: memoryID };
      return helpers.get(authURL, path, params);
    });
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

